<template>
  <header :class="['navbar', renderSidebar && 'background']">
    <div class="left-side">
      <PhList
        v-if="renderSidebar"
        class="sidebar-menu-icon"
        @click="state.openSidebar = !state.openSidebar"
      />
      <div v-if="runnerData.logoUrl || runnerData.brandName" class="brand">
        <img v-if="runnerData.logoUrl" :src="runnerData.logoUrl" class="logo-image" />
        <AntText v-if="runnerData.brandName" class="brand-name" ellipsis>
          {{ runnerData.brandName }}
        </AntText>
      </div>
    </div>
    <div class="right-side">
      <AntPopover v-if="email" placement="bottomRight">
        <template #content>
          <div style="display: flex; flex-direction: column; gap: 10px">
            <AntText size="small" type="secondary">
              {{ email }}
            </AntText>
            <AntButton type="text" @click="logout">
              <div style="display: flex; gap: 5px">
                <PhSignOut size="20" />
                Logout
              </div>
            </AntButton>
          </div>
        </template>
        <AntAvatar shape="square" size="small" :style="{ backgroundColor: mainColor }">
          {{ email[0].toUpperCase() }}
        </AntAvatar>
      </AntPopover>
      <AntButton v-else-if="!hideLogin" type="text" @click="emits('login-click')">
        <div style="display: flex; gap: 5px">
          Login
          <PhSignIn size="20" />
        </div>
      </AntButton>
    </div>
    <Sidebar
      v-if="renderSidebar"
      :current-path="currentPath"
      :runner-data="props.runnerData"
      :open="state.openSidebar"
      :loading="props.loading"
      @select-runtime="emits('navigate', $event)"
      @close-sidebar="state.openSidebar = false"
    />
  </header>
</template>

<script lang="ts" setup>
import { useUserStore } from '@/apps/player/auth/userStore';
import { logoutSideEffects } from '@/apps/player/auth/userProvider';
import Sidebar from '@/components/Sidebar.vue';
import { SidebarItem, WorkspaceRunnerData } from '@/services/runnerData';
import { PhList, PhSignIn, PhSignOut } from '@phosphor-icons/vue';
import {
  Avatar as AntAvatar,
  Button as AntButton,
  Popover as AntPopover,
  TypographyText as AntText,
} from 'ant-design-vue';
import { computed, reactive } from 'vue';

const state = reactive<{
  openSidebar: boolean;
}>({
  openSidebar: false,
});

const props = defineProps<{
  mainColor?: string;
  currentPath: string;
  hideLogin?: boolean;
  emailPlaceholder?: string;
  loading?: string | null;
  runnerData: WorkspaceRunnerData;
}>();

const emits = defineEmits<{
  (e: 'navigate', f: SidebarItem): void;
  (e: 'login-click'): void;
  (e: 'logout-click'): void;
}>();

const userStore = useUserStore();

const email = computed(() => userStore.email ?? props.emailPlaceholder);

const renderSidebar = computed(() => {
  const sidebar = props.runnerData.sidebar;
  return sidebar && sidebar.length > 0;
});

const logout = () => {
  userStore.removeJWT();
  logoutSideEffects();
  emits('logout-click');
};
</script>

