<template>
  <div :class="['sidebar', { open: open }]">
    <div class="sidebar-content">
      <div class="section">
        <div
          v-for="runtime in runnerData?.sidebar || []"
          :key="runtime.id"
          :class="['item', { active: isCurrent(runtime.path), disabled: loading === runtime.id }]"
          @click="selectRuntime(runtime)"
        >
          <Component
            :is="iconPath(runtime.type)"
            style="flex-shrink: 0; width: 22px; height: 18px"
          />
          <div class="item-title">
            {{ runtime.name }}
          </div>
          <LoadingOutlined v-if="loading === runtime.id" />
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { iconPath } from '@/apps/editor/views/workflow/state/metadata';
import type { FormRunnerData, SidebarItem, WorkspaceRunnerData } from '@/services/runnerData';
import { LoadingOutlined } from '@ant-design/icons-vue';

const emit = defineEmits(['selectRuntime', 'closeSidebar']);

const isMobile = window.innerWidth < 760;

const props = defineProps<{
  runnerData: FormRunnerData | WorkspaceRunnerData | null;
  open: boolean;
  currentPath: string;
  loading?: string | null;
}>();

const selectRuntime = (item: SidebarItem) => {
  if (isCurrent(item.id)) return;
  emit('selectRuntime', item);
  if (isMobile) emit('closeSidebar');
};

const isCurrent = (path: string) => {
  return path === props.currentPath;
};
</script>

<style lang="scss" scoped>
