  gap: 2rem;

  background: rgba(255, 255, 255);
  box-shadow: 4px 8px 4px rgba(0, 0, 0, 0.02);
  box-sizing: border-box;
  z-index: 2;

  transition: 600ms;

  &.open {
    @include sidebar-open-state();
  }

  .sidebar-content {
    display: flex;
    flex-direction: column;
    overflow: auto;
    width: 100%;

    .item {
      min-height: unset;
      cursor: pointer;
      padding: 1rem;
      box-sizing: border-box;
      display: flex;
      gap: 1rem;
      align-items: center;
      justify-content: flex-start;
      border-radius: 4px;

      .item-title {
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        user-select: none;
        line-height: 20px;
        letter-spacing: 0.1px;
        color: #49454f;
      }

      &:hover {
        background: #00000010;
